.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header{
  padding: 20px 70px;
  background-color: black;
  color: white;
  font-size: xx-large;
  display: flex;
  justify-content: space-between;
}
.cart-icon .counter{
height: 20px;
width: 20px;
background-color: blue;
border-radius: 50%;
right: 7px;
top:-6px;
font-size: 15px;
display: flex;
align-items: center;
justify-content: center;
position: relative;
}
.cart-icon{
  display: flex;
}
.cart-icon .icon{
  position: relative;
  right: 2px;
  font-size: 22px;
}
img{
  height: 150px;
}
.cart-wrapper{
display: inline-flex;

width: 320px;
box-shadow: 0px 0px 15px black;
border-radius: 5px;

}
.image-wrapper{
  width:130px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-right: 1px solid rgb(218, 215, 215);

}
.content{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap:10px;
  padding: 10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border: 2px solid #050505e2;
}
.quantity *{
  height: 20px;
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity > div:first-child , 
.quantity> div:last-child {
background-color: black;
color: white;
border-radius: 0;
}
.quantity > button:disabled{
  opacity: 0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
align-items: center;
  justify-content: center;
  align-content: center;
  gap:30px;
}
.footer{
  padding:20px 180vh;
  /*box-sizing: border-box;
  padding:17px 30px*/
  color: white;
  font-size: 20px ;
  background-color: black;
  
  position: fixed;
  bottom: 0;
  
  
  width: 100%;
}
